	module.exports = {"data":{"challengeNode":{"title":"Problem 174: Counting the number of \"hollow\" square laminae that can form one, two, three, ... distinct arrangements","description":["We shall define a square lamina to be a square outline with a square \"hole\" so that the shape possesses vertical and horizontal symmetry.","Given eight tiles it is possible to form a lamina in only one way: 3x3 square with a 1x1 hole in the middle. However, using thirty-two tiles it is possible to form two distinct laminae.","","","If t represents the number of tiles used, we shall say that t = 8 is type L(1) and t = 32 is type L(2).","Let N(n) be the number of t ≤ 1000000 such that t is type L(n); for example, N(15) = 832.","What is ∑ N(n) for 1 ≤ n ≤ 10?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-174-counting-the-number-of-hollow-square-laminae-that-can-form-one-two-three-----distinct-arrangements","blockName":"Project Euler","tests":[{"text":"<code>euler174()</code> should return 209566.","testString":"assert.strictEqual(euler174(), 209566, '<code>euler174()</code> should return 209566.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler174() {\n  // Good luck!\n  return true;\n}\n\neuler174();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-175-fractions-involving-the-number-of-different-ways-a-number-can-be-expressed-as-a-sum-of-powers-of-2","id":"5900f41a1000cf542c50ff2d"},"slug":"/coding-interview-prep/project-euler/problem-174-counting-the-number-of-hollow-square-laminae-that-can-form-one-two-three-----distinct-arrangements"}}

